.home__container {
    max-width: 1500px;
    margin: 0px auto;
}

.home__detail {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}

.home__detail img {
    width: 630px;
}

.home__detailLeft {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.home__detailLeft span {
    color: var(--primary-color);
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 10px;
}

.home__detailLeft h2 {
    font-weight: 300;
    font-size: 55px;
    line-height: 65px;
    color: #35385C;
}

.home__detailbutton {
    display: flex;
    margin-top: 30px;
}

.dailyActive {
    margin-top: 50px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    font-size: 22px;
    font-weight: 400;
}

.dailyActive span {
    color: var(--primary-color);
    margin-right: 7px;
}

.searchFeatureBanner {
    /* ff 3.6+ */
    background:-moz-linear-gradient(90deg, rgba(0, 60, 249, 1) 0%, rgba(3, 36, 142, 1) 50%, rgba(4, 10, 25, 1) 100%); 

    /* safari 5.1+,chrome 10+ */
    background:-webkit-linear-gradient(90deg, rgba(0, 60, 249, 1) 0%, rgba(3, 36, 142, 1) 50%, rgba(4, 10, 25, 1) 100%);

    /* opera 11.10+ */
    background:-o-linear-gradient(90deg, rgba(0, 60, 249, 1) 0%, rgba(3, 36, 142, 1) 50%, rgba(4, 10, 25, 1) 100%);

    /* ie 6-9 */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#040A19', endColorstr='#003CF9', GradientType=0 );

    /* ie 10+ */
    background:-ms-linear-gradient(90deg, rgba(0, 60, 249, 1) 0%, rgba(3, 36, 142, 1) 50%, rgba(4, 10, 25, 1) 100%);

    /* global 94%+ browsers support */
    background:linear-gradient(90deg, rgba(0, 60, 249, 1) 0%, rgba(3, 36, 142, 1) 50%, rgba(4, 10, 25, 1) 100%);
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
    border-radius: 15px;
    padding: 30px;
    margin: 30px;
}

.searchFeatureBanner img {
    width: 520px;
}

.searchFeatureBanner div {
    margin-top: 30px;
}

.searchFeatureBanner div h1 {
    color: white;
    font-weight: 300;
    font-size: 38px;
    margin-bottom: 10px;
}

.searchFeatureBanner div span {
    font-weight: 500;
    font-size: 18px;
    color: white;
}

/* 223D93 */

.searchFeatureContent {
    margin-top: 80px;
    color: white;
}

.searchFeatureContent div {
    margin-bottom: 20px;
    font-size: 18px;
    display: flex;
    align-items: center;
}

.searchFeatureContent div span {
    margin-right: 10px;
    padding: 5px;
    border-top:  .1px solid rgb(173, 173, 173);
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    background:-moz-radial-gradient(circle at 26% 98%, rgba(44, 70, 150, 1) 0%, rgba(97, 115, 174, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 26% 98%, rgba(44, 70, 150, 1) 0%, rgba(97, 115, 174, 1) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 26% 98%, rgba(44, 70, 150, 1) 0%, rgba(97, 115, 174, 1) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(circle at 26% 98%, rgba(44, 70, 150, 1) 0%, rgba(97, 115, 174, 1) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 26% 98%, rgba(44, 70, 150, 1) 0%, rgba(97, 115, 174, 1) 100%);
    box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 1px, rgba(0, 0, 0, 0.07) 0px 2px 2px, rgba(0, 0, 0, 0.07) 0px 4px 4px, rgba(0, 0, 0, 0.07) 0px 8px 8px, rgba(0, 0, 0, 0.07) 0px 16px 16px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.searchFeatureContent div p {
    font-weight: 500;
    font-size: 20px;
}

.stats {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}

.stats div {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.stats div p {
    font-size: 22px;
    color: var(--primary-color);
}

.stats div span {
    color: var(--secondary-color);
    font-size: 14px;
    margin-bottom: 15px;
}

.stats div img {
    width: 120px;
}

.businessApplication {
    margin-top: 120px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
}

.businessApplication h1 {
    text-align: center;
    width: 40%;
    font-weight: 300;
    letter-spacing: 1.5px;
    color: var(--secondary-color);
}

.businessApplication__useCaseContainer {
    display: flex;
    flex-direction: column;
}

.businessApplication div {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}


.testimonyContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 150px;
}


.testimonyContainer h1 {
    margin-bottom: 30px;
    font-weight: 300;
    color: var(--secondary-color);
}

.testimony__companyImageContainer {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 90%;
    border-radius: 20px;
    background:-moz-radial-gradient(ellipse at 25% 70%, rgba(11, 21, 53, 1) 0%, rgba(5, 29, 103, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(ellipse at 25% 70%, rgba(11, 21, 53, 1) 0%, rgba(5, 29, 103, 1) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(ellipse at 25% 70%, rgba(11, 21, 53, 1) 0%, rgba(5, 29, 103, 1) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(ellipse at 25% 70%, rgba(11, 21, 53, 1) 0%, rgba(5, 29, 103, 1) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(ellipse at 25% 70%, rgba(11, 21, 53, 1) 0%, rgba(5, 29, 103, 1) 100%);
}

.testimony__companyImageContainer img {
    width: 120px;
    height: 20px;
    object-fit: contain;
    margin: 20px 10px;
    background-clip: text;-webkit-background-clip: text;color: transparent;
}